			}],
			// 默认
			list1:[],
			loadtext1:"上拉加载更多",
			// 最新
			list2:[],
			loadtext2:"上拉加载更多",
		}
	},
	computed: {
		// 当前列表数据
		listData() {
			// return this['list'+this.tabIndex+1]
			if (this.tabIndex === 0) {
				return this.list1
			}
			return this.list2
		},
		// 当前上拉加载
		loadtext(){
			if (this.tabIndex === 0) {
				return this.loadtext1
			}
			return this.loadtext2
		}
	},
	onLoad(e) {
		if (e.detail) {
