		}
		this.list1 = demo
	},
	// 触底事件
	onReachBottom() {
		this.loadmore()
	},
	methods: {
		// tab切换
		changeTab(index){
			this.tabIndex = index
		},
		// 上拉加载更多
		loadmore(){
			// 拿到当前的列表
			let index = this.tabIndex
			// 判断是否处于可加载状态
			if (this.loadtext !== '上拉加载更多') return;
			// 设置上拉加载状态处于加载中...
			this['loadtext'+(index+1)] = '加载中...'
			// 请求数据
			setTimeout(()=>{
				this['list'+(index+1)] = [...this['list'+(index+1)],...this['list'+(index+1)]]
				this['loadtext'+(index+1)] = '上拉加载更多'
			},2000)
		}
	}
